.App {
  width: 100%;
  height: 100vh;
  font-size: 14px;
  background-color: #00B2FF;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;

  span {
    display: block;
  }

  .all-sections {
    width: fit-content;
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 8px;
  }

  
  .com-wrapper {
    padding: 8px 16px;
    width: 100%;
    background-color: white;
    box-sizing: border-box;

    &:first-of-type {
      border-radius: 8px 8px 0 0;
    }

    &:last-of-type {
      border-radius: 0 0 8px 8px;
    }
  }

  .comp-title-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    width: fit-content;
  }

  .comp-title {
    font-size: 16px;
  }

  .title-underline {
    width: 100%;
    height: 2px;
    background-color: #764abc;
  }

  input {
    background-color: white;
    border: 1px solid black;
    width: 60px;
    height: 30px;
    padding: 2px;
    text-align: center;
  }
  
  .comp-title {
    margin-bottom: 2px;
  }
  
  .rgb-code-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}